.projects__description {
  font-size: 12px;
  color: #333333;
  font-family: 'roboto_condensedlight';
  text-transform: uppercase;
  line-height: 1;
  margin: 0; }
  .projects__description b {
    font-family: 'roboto_condensedbold';
    font-weight: normal; }

.projects__description--lowercase {
  text-transform: initial; }

.projects__view {
  font-size: 18px;
  color: #333333;
  font-family: 'roboto_condensedlight';
  text-transform: uppercase;
  line-height: 1;
  display: inline-block;
  background: url("/img/arrow-view.svg") left center no-repeat;
  padding-left: 25px;
  padding-right: 25px;
  text-decoration: none; }
  .projects__view span {
    border-bottom: 1px solid transparent;
    transition-property: border-color;
    transition-duration: 500ms; }
  .projects__view:hover span {
    border-color: #333333; }

.projects__like {
  max-width: 145px;
  text-align: center;
  margin: 22.3% 0 0 0; }

.like__btn {
  background: none;
  border: none;
  box-shadow: none;
  cursor: pointer;
  padding: 0; }
  .like__btn svg {
    width: 24px;
    height: 22px;
    transition-duration: 500ms;
    transition-property: fill; }
  .like__btn:hover svg {
    fill: #f00; }

.like__counts {
  font-size: 11px;
  font-family: 'roboto_condensedlight';
  color: #f00;
  text-transform: uppercase;
  line-height: 1.091;
  display: block;
  margin: 5px 0 0 0; }

.links {
  list-style: none;
  margin: 0;
  padding: 0; }

.links__link {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.077;
  text-decoration: none;
  box-sizing: border-box;
  padding-left: 25px;
  transition-property: color;
  transition-duration: 500ms; }
  .links__link:hover {
    color: #000;
    background: url("/img/arrow.svg") left center no-repeat; }
